
import { PropType, defineComponent } from 'vue'
import { IbUserDetail } from '@/shared/type/CIB20100311.type'
import { CIB20100338_REQ_BODY, CIB20100338_RES } from '@/shared/type/CIB20100338.type'
import { modalController } from '@ionic/vue';
import { screenMode } from "@/shared/type/Approval.type";
import { AuthorizationDetail } from '@/shared/type/APM20200113.type';

export default defineComponent({
  name: "cib201003ss09",

  props: {
    data: {
      type: Object as PropType<IbUserDetail>,
      required: true
    },
    userID: {
      type: String,
      required: true
    },
    approvalPayload: {
      type: Object as PropType<AuthorizationDetail>,
      required: false
    },
    currentTab: {
      type: String,
      required: true,
    }
  },

  data() {
    return {
      tempData: {
        customerNo: "",
        ibPolicyConfiguration: {
          userID:"",
          subscriptionApprovalTypeCode: "",
          authorizationLinePolicyTypeCode: "",
          authorizationLineAssignTypeCode: ""
        }
      },
      isDisableAuthorizationLinePolicyTypeCode: false,
      isDisableAuthorizationLineAssignTypeCode: false,
      approvalRes: {} as any,
			operationMode: 'normal' as screenMode,
      isEditable: false,
      tempPolicyConfiguration:{
        subscriptionApprovalTypeCode: "",
        authorizationLinePolicyTypeCode: "",
        authorizationLineAssignTypeCode: ""
      }
    }
  },

  watch: {
    data(customerData: IbUserDetail) {
      if(this.approvalType == ''){
        this.tempData = JSON.parse(JSON.stringify({
          customerNo: customerData.userInfo.customerNo,
          ibPolicyConfiguration: {
            userID: customerData.userInfo.userID,
            subscriptionApprovalTypeCode: customerData.userInfo.subscriptionApprovalTypeCode,
            authorizationLinePolicyTypeCode: customerData.userInfo.authorizationLinePolicyTypeCode,
            authorizationLineAssignTypeCode: customerData.userInfo.authorizationLineAssignTypeCode
          }
        }))
        this.tempPolicyConfiguration.subscriptionApprovalTypeCode = customerData.userInfo.subscriptionApprovalTypeCode
        this.tempPolicyConfiguration.authorizationLinePolicyTypeCode = customerData.userInfo.authorizationLinePolicyTypeCode
        this.tempPolicyConfiguration.authorizationLineAssignTypeCode = customerData.userInfo.authorizationLineAssignTypeCode
      }else if(this.approvalPayload){
        if(this.currentTab == 'CIB201003SS09'){
          let authDetailParsed = JSON.parse(this.approvalPayload?.requestContent)
          this.tempData.ibPolicyConfiguration.subscriptionApprovalTypeCode = authDetailParsed.subscriptionApprovalTypeCode;
          this.tempData.ibPolicyConfiguration.authorizationLinePolicyTypeCode = authDetailParsed.authorizationLinePolicyTypeCode;
          this.tempData.ibPolicyConfiguration.authorizationLineAssignTypeCode = authDetailParsed.authorizationLineAssignTypeCode;
          this.operationMode = this.approvalType == 'my-request' ? 'read' : this.approvalType == 'my-approval' ? 'approval' : 'normal';
        }
      }
    },

    'tempData.ibPolicyConfiguration.subscriptionApprovalTypeCode': function(newValue){
      if(newValue == '02'){
        this.tempData.ibPolicyConfiguration.authorizationLinePolicyTypeCode = '01';
        this.tempData.ibPolicyConfiguration.authorizationLineAssignTypeCode = '01';
        this.isDisableAuthorizationLinePolicyTypeCode = true;
        this.isDisableAuthorizationLineAssignTypeCode = true;
      }else{
        this.isDisableAuthorizationLinePolicyTypeCode = false;
        this.isDisableAuthorizationLineAssignTypeCode = false;
      }
    },

    isEditable(isAllowTabChange) {
      this.$emit('isBeingEdited', isAllowTabChange);
    }
  },

  computed: {
		approvalType(): string {
			return this.$route.query.approvalType ? this.$route.query.approvalType.toString() : '';
		},
  },


  methods: {
    onClickOperation(btn: string) {
      switch (btn) {
        case "edit":
          this.isEditable = true;
          break;

        case "cancel": 
          this.tempData.ibPolicyConfiguration.subscriptionApprovalTypeCode = this.tempPolicyConfiguration.subscriptionApprovalTypeCode
          this.tempData.ibPolicyConfiguration.authorizationLinePolicyTypeCode =  this.tempPolicyConfiguration.authorizationLinePolicyTypeCode 
          this.tempData.ibPolicyConfiguration.authorizationLineAssignTypeCode = this.tempPolicyConfiguration.authorizationLineAssignTypeCode
          this.isEditable = false;
          break;

        case "list":
          this.$router.push('/corporate-internet-banking/subscription');
          break;

        case "submit":
          this.$dialog.onOpen({
            content: `${this.$t("message.would_you_like_to_save")}`,
            dialogMode:"Question",
            callback: ()=>{
              this.handleSubmit();
              modalController.dismiss();
            }
          })
          break;
      }
    },

    async handleSubmit() {
      const reqBody: CIB20100338_REQ_BODY = {
        customerNo: this.tempData.customerNo,
        userID: this.tempData.ibPolicyConfiguration.userID,
        subscriptionApprovalTypeCode: this.tempData.ibPolicyConfiguration.subscriptionApprovalTypeCode,
        authorizationLineAssignTypeCode: this.tempData.ibPolicyConfiguration.authorizationLineAssignTypeCode,
        authorizationLinePolicyTypeCode: this.tempData.ibPolicyConfiguration.authorizationLinePolicyTypeCode
      };
    
      const responseData = await this.$request.getRequest('CIB20100338', reqBody, false, 'CIB201003SS09') as CIB20100338_RES;
      if(this.approvalType){
        this.isEditable = false;
        this.$emit("isReseTButton", false);
      }

      if (responseData.header.result){
        this.isEditable = false;
        this.$emit("isReseTButton", false);
        this.$dialog.onOpen({
          content: this.$t("message.update_success"),
          dialogMode:"Success",
          callback:()=>{
            modalController.dismiss()
          }
        });
      }
    }
  }
})
